<template>
  <nav class="nav">
    <div class="header">
      <div class="flex-item toggle-button">
        <a class="toggle" v-on:click="toggleSidebar">
          <font-awesome-icon class="icon" icon="bars" />
        </a>
      </div>

      <div class="flex-item logo">
        <router-link to="/" class="logo">
          <img src="@/assets/logo-small.svg" />
        </router-link>
      </div>

      <div class="flex-item filler"></div>
    </div>

    <transition name="slide">
      <div class="sidebar" v-hammer:swipe.right="toggleSidebar" :class="{ toggled: !sidebarHidden }" v-if="!sidebarHidden">
        <div class="container">
          <ul class="pure-menu-list" v-on:click="sidebarHidden = true">
            <li class="navItem pure-menu-item"><router-link class="pure-menu-link" to="/articles?tags=Robots">Robots</router-link></li>
            <li class="navItem pure-menu-item"><router-link class="pure-menu-link" to="/articles">Articles</router-link></li>
            <li class="navItem pure-menu-item"><router-link class="pure-menu-link" to="/contact">Contact</router-link></li>
            <li class="navItem pure-menu-item"><router-link class="pure-menu-link" to="/sponsors">Sponsors</router-link></li>
          </ul>
        </div>
      </div>
    </transition>

    <transition name="fade">
      <div class="sidebar-background" v-if="!sidebarHidden" v-on:click="sidebarHidden = true"></div>
    </transition>
  </nav>
</template>

<script>
import eventBus from '@/events'

export default {
  data () {
    return {
      sidebarHidden: true
    }
  },

  mounted () {
    eventBus.$on('mobile-navbar-toggle', this.toggleSidebar)
  },

  destroyed () {
    eventBus.$off('mobile-navbar-toggle', this.toggleSidebar)
  },

  methods: {
    toggleSidebar () {
      this.sidebarHidden = !this.sidebarHidden
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../style/variables";

.logo {
  display: flex;

  img {
    display: flex;
  }
}

.slide-leave-active,
.slide-enter-active {
  transition: .5s;
}

.slide-enter, .slide-leave-to {
  transform: translate(-100%, 0);
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}

.fade-enter, .fade-leave-to {
  opacity: 0;
}

.header {
    z-index: 4;
    position: fixed;

    width: 100vw;
    top: 0;

    height: $head-height;

    display: flex;
    flex: 1;

    align-items: center;
    justify-content: flex-start;

    .flex-item {
      display: flex;

      &.toggle-button, &.filler {
        flex: 1;
        padding-left: 20px;
      }

      &.logo {
        img {
          height: $head-logo-height;
        }

        justify-content: flex-end;
      }

      &.toggle-button {
        font-size: 2em;
        color: white;
      }
    }
}

.sidebar {
  z-index: 3;
  position: fixed;
  top: 0;
  left: 0;

  float: left;

  background: black;
  color: white;

  height: 100vh;
  width: 80vw;

  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.75);

  .container {
    padding: 92px 12px;
  }
  .pure-menu-link {
    &:hover, &:focus {
      background-color: transparent;
      color: #2A9051;
    }
  }
}

.sidebar-background {
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(5px);
  z-index: 2;

  position: fixed;
  top: 0;
  left: 0;

  height: 100vh;
  width: 100vh;
}

.header {
    background: #1b1b1b;
    width: 100%;
}
</style>
